.event-shooting .content-container {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.event-shooting__title {
	padding-top: 71px;
	text-align: center;

	font-family: 'MaruBuri';
	font-style: normal;
	font-weight: 700;
	font-size: 35px;
	line-height: 47px;
	/* identical to box height */

	letter-spacing: -0.015em;

	color: #212529;
}
.event-shooting__button--closing {
	position: absolute;
	width: 48px;
	height: 48px;
	right: 20vw;
	top: 70px;
	cursor: pointer;
}
.event-shooting__head-background {
	margin-top: 70px;
	width: 100%;
	height: 316.98px;
	background-image: url(./../images/event_shooting/event_shooting_img_000.jpg);
}
.event-shooting__subtitle {
	text-align: center;
	margin-top: 70px;
	font-family: 'Noto Sans KR';
	font-style: normal;
	font-weight: 700;
	font-size: 35px;
	line-height: 51px;

	letter-spacing: -0.015em;

	color: #212529;
}
.event-shooting__content-wrapper {
	margin-top: 30px;
	display: flex;
	justify-content: center;
	gap: 30px;
	flex-wrap: wrap;
	margin-bottom: 70px;
}
.event-shooting__card-wrapper {
	flex-direction: column;
	width: 460px;
	height: 490px;
	background: #ffffff;
	border: 1px solid #dfdede;
	border-radius: 20px;
	margin-top: 40px;

	-webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

	filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.event-shooting__card-wrapper .event-shooting__card-img {
	padding-top: 25px;
	width: 401.03px;
	height: 220px;
	margin-left: 29.5px;
}
.event-shooting__card-wrapper .event-shooting__card-borderline {
	background-image: linear-gradient(
		to right,
		#ff2068 50%,
		rgba(255, 255, 255, 0) 0%
	);
	background-position: bottom;
	background-size: 16px 1px;
	background-repeat: repeat-x;
	padding-bottom: 14px;
	margin-bottom: 11px;
	width: 200.51px;
	margin-left: 29.5px;
}
.event-shooting__card-wrapper .event-shooting__card-title {
	font-family: 'MaruBuri';
	font-style: normal;
	font-weight: 600;
	font-size: 20px;
	line-height: 36px;
	margin-top: 15px;
	/* identical to box height, or 180% */

	letter-spacing: -0.015em;

	color: #3c3243;
	margin-left: 29.5px;
}
.event-shooting__card-wrapper .event-shooting__card-maintext {
	font-family: 'Noto Sans KR';
	font-style: normal;
	font-weight: 400;
	font-size: 18px;
	line-height: 180%;
	/* or 29px */

	letter-spacing: -0.015em;

	color: #3c3243;
	margin-left: 29.5px;
}
